























































import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import Page from '@/components/common-components/page.vue'
import pageView from '@/components/common-ui/page-view.vue'
import navView from '@/components/nav/index.vue'
@Component({
  components: {
    Page,
    pageView,
    navView
  }
})
export default class Home extends Vue {
  $route: any
  $createToast : any
  $createDialog: any
  point = {}
  // 声明周期钩子
  fleetId : any
  title: any
  showMore = true
  currentTab = ''
  navList = [{
    label: '出发点位',
    value: 'start'
  }, {
    label: '到达点位',
    value: 'end'
  }]
  pointObj = {
    start: [],
    end: []
  }

  timeRows = []
  created () {
    this.pointList = []
    this.fleetId = this.$route.query.fleetId
    this.title = '点位列表'
    this.getFleetInfoById(this.fleetId)
  }

  get showTimes () {
    return this.timeRows && this.timeRows.length > 0
  }
  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  // get showMore () {
  //   return Math.ceil(this.count / this.limit) >= (this.page + 1)
  // }
  getFleetInfoById (id : any) {
    const toast = this['$createToast']({
      time: 0,
      txt: '加载...'
    })
    toast.show()
    this.$store.dispatch('getFleetInfoById', { id: id }).then((response) => {
      if (response.success) {
        console.log(response, 'oooooooooo')
        let data = response.data;
        let point = data.point || {
          start: [],
          end: []
        }
        this.pointObj = point;
        this.$set(this, 'timeRows', data.timeRows)
      }  
      toast.hide()
    })
  }

  

  // addPoint () {
  //   this.$router.push({ name: 'mapSelect', query: {} })
  // }

  dialog = null;
  getTime(index){
    if (!this.showTimes) {
      return ''
    }

    let currentIndex = this.currentTab === 'start' ? index : this.pointObj.start.length + index;
    if (!this.timeRows[currentIndex]) {
      return ''
    }
    let cObj = this.timeRows[currentIndex][currentIndex + 1]
    if (cObj) {
      return '去下个点位  预估路程：' + Math.floor(cObj.distance/1000) + '公里  ' + '预估时间：' + this.secondsToTime(cObj.duration)
    } else {
      return '此点为终点站~'
    }
  }
  addPoint () {
    this.pointTextDialot('')
  }

  secondsToTime(seconds) {
    var hours, minutes, remainingSeconds;
    hours = Math.floor(seconds / 3600);
    remainingSeconds = seconds % 3600;
    minutes = Math.floor(remainingSeconds / 60);
    remainingSeconds = remainingSeconds % 60;
    let text = ''
    if (hours) {
      text = text + hours + '小时'
    }
    if (minutes) {
      text = text + minutes + '分钟'
    }
    if (remainingSeconds) {
      text = text + remainingSeconds + '秒'
    }
    return text
}
  savePoint () {
    console.log(this.pointObj)
    let validateState = this.validate()
    if (!validateState) {
      return
    }
    const toast = this['$createToast']({
      time: 0,
      txt: '保存中...'
    })
    toast.show()
    this.$store.dispatch('updateFleetInfoById', { id: this.fleetId, key: 'point', data: this.pointObj }).then((response) => {
      if (response.success) {
        console.log(response, 'oooooooooo')
        this.$set(this, 'timeRows', [])
        this.$createDialog({
          type: 'alert',
          title: '提示',
          content: '保存成功！',
          icon: 'cubeic-alert'
        }).show()
      }  
      toast.hide()
    })
  }

  validate () {
    let state = true;
    let message = ''
    Object.keys(this.pointObj).forEach((key)=>{
      if (this.pointObj[key].length === 0) {
        state = false
        message = key === 'start' ? '出发点位必填' : '到达点位必填'
      } else {
        this.pointObj[key].forEach((item) => {
          if (!item.point || !item.point.location) {
            state = false
            message = item.label + '未添加定位！'
          } 
        })
      }
    })

    if (!state) {
      this.$createDialog({
        type: 'alert',
        title: '提示',
        content: message,
        icon: 'cubeic-alert'
      }).show()
    }
    return state;
  }
  addOrUpdatePoint (index) {
    let currentPoint = this.pointObj[this.currentTab][index]
    let currentType = currentPoint.point ? 'update' : 'add'
    let currentTabType = this.currentTab;
    this.$router.push({ name: 'mapSelect', query: {
      backInfo: {
        updateIndex: index,
        updateType: currentTabType 
      },
      location: currentType === 'update'? currentPoint.point.location : []
    } })
  }

  addOrUpdateRegion (index) {
   let currentPoint = this.pointObj[this.currentTab][index]
    let currentType = currentPoint.point ? 'update' : 'add'
    let currentTabType = this.currentTab;
    this.$router.push({ name: 'regionSelect', query: {
      backInfo: {
        updateIndex: index,
        updateType: currentTabType 
      },
      location: currentType === 'update'? currentPoint.point.location : []
    } })
  }

  configLine (index, item) {
    console.log(item, 'vvvvvvvvvvvvvvvvvvvvvvv')
    let currentTabType = this.currentTab;
    this.$router.push({ name: 'lineSelect', query: {
      backInfo: {
        updateIndex: index,
        updateType: currentTabType 
      },
      mateList: item.mateList || [],
      fleetId: this.fleetId,
      updateType: currentTabType
    } })
  }

  deletePoint (index) {
    let dialog = this.$createDialog({
      type: 'confirm',
      title: '删除确认',
      content: '确认删除该点位？',
      confirmBtn: {
        text: '确认',
        active: true,
        disabled: false,
        href: 'javascript:;'
      },
      cancelBtn: {
        text: '取消',
        active: false,
        disabled: false,
        href: 'javascript:;'
      },
      onConfirm: () => {
        this.pointObj[this.currentTab].splice(index, 1)
      },
      onCancel: () => { }
    })
    dialog.show()
  }

  updateDialotText (value, index) {
    this.pointTextDialot(value, 'update', index)
  }

  pointTextDialot (value, type, index) {
    let setValue = value ? value : '';
    this.dialog = this.$createDialog({
      type: 'prompt',
      title: value ? '修改点位' : '添加点位',
      prompt: {
        value: setValue,
        placeholder: '请输入'
      },
      onConfirm: (e, promptValue) => {
        if (promptValue) {
          if (type) {
            let obj = this.pointObj[this.currentTab][index]
            obj.label = promptValue
            this.pointObj[this.currentTab]
            this.$set(this.pointObj[this.currentTab], index, obj)
          } else {
            this.pointObj[this.currentTab].push({
              label: promptValue
            })
          }
        }
      }
    }).show()
  }
  navChange (value) {
    console.log(value)
    this.currentTab = value
  }

  @Watch('$route')
    routeChange (newVal) {
     let backData = this.$store.state.backData;
    if (backData.route === 'point') {
      let info = backData.data
      let backInfo = info.backInfo;
      this.$set(this.pointObj[backInfo.updateType][backInfo.updateIndex], 'point', {
        label: info.label || '未设置定位',
        location: info.coordinate
      })
    } else if (backData.route === 'lineConfig') {
      let info = backData.data
      let backInfo = info.backInfo;
      this.$set(this.pointObj[backInfo.updateType][backInfo.updateIndex], 'mateList', info.selectList)
    }
   }

}
